import cmdIcon from '../cmd-icon/cmd-icon.vue'
export default {
  name: 'cmd-notice-bar',

  components: {
    cmdIcon
  },

  props: {
    /**
     * 显示文本
     */
    text: {
      tyep: String,
      default: ''
    },
    /**
     * 右侧提示类型 close/link
     */
    mode: {
      type: String,
      default: ''
    },
    /**
     * 情景类型 default/activity/warning
     */
    type: {
      type: String,
      default: 'default'
    },
    /**
     * 显示时长 单位为ms，不需要自动消失可将其置为0
     */
    time: {
      type: Number,
      default: 0,
    },
    /**
     * 圆角显示
     */
    round: {
      type: Boolean,
      default: false,
    },
    /**
     * 多行文本显示
     */
    rows: {
      type: Boolean,
      default: false,
    },
    /**
     * 滚动文本
     */
    scrollable: {
      type: Boolean,
      default: false,
    },
    /**
     * 左侧状态图标
     */
    icon: {
      type: String,
      default: '',
    },
    /**
     * 通知栏自定义样式
     */
    make: {
      type: Object,
      default: () => {
        return {
          'background-color': '',
          'color': ''
        }
      }
    }
  },

  data() {
    return {
      /**
       * 通知栏显示
       */
      isShow: true
    }
  },

  computed: {
    /**
     * 右侧提示图标
     */
    setModeIcon() {
      if (this.mode === 'link') {
        return 'chevron-right';
      } else if (this.mode === 'close') {
        return 'close';
      }
    },
    /**
     * 左侧提示图标
     */
    setIconColor() {
      let iconColor = '';
      if (this.type === 'activity') {
        iconColor = '#FF843D';
      } else if (this.type === 'warning') {
        iconColor = '#FF5B60';
      } else {
        iconColor = '#2F86F6';
      }
      return iconColor;
    },
    /**
     * 自定义style
     */
    setNoticeStyle() {
      let noticeStyle = '';
      for (let it in this.make) {
        noticeStyle += `${it}:${this.make[it]};`;
      }
      return noticeStyle;
    },
    /**
     * 单行文本溢出省略
     */
    setSingleEllStyle() {
      if (!this.rows && !this.scrollable) {
        return 'text-overflow: ellipsis;white-space:nowrap;overflow:hidden;';
      }
      return '';
    }
  },

  mounted() {
    if (this.time) {
      this.$_hide(this.time)
    }
  },

  methods: {
    /**
     * 定时关闭通知栏
     */
    $_hide(time) {
      setTimeout(() => {
        this.isShow = false
      }, time)
    },
    /**
     * 右侧图标点击事件
     */
    $_mode(e) {
      if (this.mode === 'close') {
        this.isShow = false;
        this.$emit('close', e)
      }
      if (this.mode === 'link') {
        this.$emit('link', e)
      }
    },
    /**
     * 通知栏文字点击
     */
    $_click(e) {
      this.$emit('click', e)
    }
  },
}
